import { Grid, TextField } from "@mui/material";
import ImageField from "../../common/imageFeild/ImageField";
import Header from "../../common/Header/Header";
import IconMenu from "../../common/Menu/Menu";
import LabelField from "../../common/labelField/LabelField";
import MediaCard from "../../common/Card/MediaCard";
import MediaCard1 from "../../common/Card/MediaCard1";
import LinearChart from "../../common/Chart/LinearChart";
import TableData from "../../common/TableData/TableData";
import Button from '@mui/material/Button';
import MediaCard2 from "../../common/Card/MediaCard2";
import MediaCard3 from "../../common/Card/MediaCard3";
import Head from "../../common/Header/Head";

const logo = require("../../images/medical.jpg")
export default function Dashboard() {
    const logo1 = {
        src: logo
    }
    return (<>
        <Grid container spacing={2}>
            <Grid item xs={12} md={3} lg={3} >
                <Grid container padding={2} >
                    <Grid padding={2} xs={12} md={12} lg={12} item>
                        <ImageField image={logo1}></ImageField>
                    </Grid>
                    <Grid padding={2} xs={12} md={12} lg={12} item>
                    <div><Button disabled>MEDICINE</Button></div>
                    <div><Button to="#text-buttons">Dashboard</Button></div>
                    <div><Button to="#text-buttons">Appointment</Button></div>
                    <div><Button to="#text-buttons">Doctors</Button></div>
                    <div><Button to="#text-buttons">Departments</Button></div>
                    <div><Button to="#text-buttons">Petients</Button></div>
                    <div><Button to="#text-buttons">Payments</Button></div>
                    </Grid>
                    <Grid padding={2} xs={12} md={12} lg={12} item>
                    
                    </Grid>
                </Grid>
            </Grid>

            <Grid item xs={12} md={9} lg={9}>
                <Grid container padding={2} spacing={2}>
                    <Grid padding={2} xs={12} md={12} lg={12} item>
                        <Header position="fixed"></Header>
                        {/* <Head></Head> */}
                    </Grid>
                    <Grid sx={{ display: 'flex' }} padding={2} xs={12} md={12} lg={12} item>
                        <Grid padding={1} xs={12} md={3} lg={3} item>
                            <MediaCard></MediaCard>
                        </Grid>
                        <Grid padding={1} xs={12} md={3} lg={3} item>
                            <MediaCard1></MediaCard1>
                        </Grid>
                        <Grid padding={1} xs={12} md={3} lg={3} item>
                            <MediaCard2></MediaCard2>
                        </Grid>
                        <Grid padding={1} xs={12} md={3} lg={3} item>
                            <MediaCard3></MediaCard3>
                        </Grid>
                    </Grid>
                    <Grid>
           <TableData></TableData>
                    </Grid>
                </Grid>

            </Grid>
        </Grid>

    </>)
}