import * as React from 'react';
import { useTheme } from '@mui/material/styles';
import Box from '@mui/material/Box';
import Card from '@mui/material/Card';
import CardContent from '@mui/material/CardContent';
import Typography from '@mui/material/Typography';

import PriceCheckIcon from '@mui/icons-material/PriceCheck';


export default function MediaCard3(props) {
  const theme = useTheme();

  return (
    <Card  sx={{ display: 'flex' ,height:70}}>
      <Box  sx={{ display: 'flex', flexDirection: 'column' }}>
        <CardContent>
          <PriceCheckIcon sx={{height:50,width:75,color:"blue",fonySize:"large"}}
          ></PriceCheckIcon>
         </CardContent>
      </Box>
      <Box sx={{  flexDirection: 'column' }} >
        <Typography sx={{fontWeight:600,fontSize:14 }} >Earning</Typography>
        <Typography padding={1} sx={{color:"blue",fontWeight:400,fontSize:22 }}>23</Typography>
        </Box>
    
    </Card>
  );
}